<template>
    <div class="arrows mobile-hidden">
        <button type="button" @click="scroll('left')" aria-label="Scroll left"
                :disabled="rowScroll === 0"
        >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 512">
                <path d="M31.7 239l136-136c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9L127.9 256l96.4 96.4c9.4 9.4 9.4 24.6 0 33.9L201.7 409c-9.4 9.4-24.6 9.4-33.9 0l-136-136c-9.5-9.4-9.5-24.6-.1-34z"/>
            </svg>
        </button>
        <button type="button" @click="scroll('right')" aria-label="Scroll right"
                :disabled="rowScroll === (0 - rowWidth)">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 512">
                <path d="M224.3 273l-136 136c-9.4 9.4-24.6 9.4-33.9 0l-22.6-22.6c-9.4-9.4-9.4-24.6 0-33.9l96.4-96.4-96.4-96.4c-9.4-9.4-9.4-24.6 0-33.9L54.3 103c9.4-9.4 24.6-9.4 33.9 0l136 136c9.5 9.4 9.5 24.6.1 34z"/>
            </svg>
        </button>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import store from '../store'

    export default {
        computed: {
            ...mapState(['rowScroll', 'rowWidth']),
        },
        methods: {
            scroll(v) {
                store.commit('scroll', v)
            }

        }
    }
</script>
